import React, { Component } from "react";
import Fade from "react-reveal";

import HakeBtn from "../Items/HakeBtn";
import GithubBtn from "../Items/GithubBtn";


class PaperItem extends Component {
  render() {
    const title = this.props.title;
    const author = this.props.author;
    const conf = this.props.conf;

    const shortname = (this.props.shortname ?
      (<span>{this.props.shortname} &bull; </span>)
      :
      (<span></span>)
    );

    const githubStars = (this.props.github ?
      (<GithubBtn href={this.props.github} />)
      :
      (<span></span>)
    );

    const links = Object.entries(this.props.links).map(function (item) {
      return (<span key={item[0]}><a href={item[1]} style={{"textDecoration": "underline"}}>[{item[0]}]</a> </span>);
    });


    const body = (this.props.children ?
      (<div>{this.props.children}</div>)
      :
      (<span></span>)
    );


    return (
      <div key={title} style={{"marginBottom": "20px"}}>
        <h4>{title}</h4>
        {author}
        <br/>
        <b>{conf}</b> &bull; {shortname} {links} {githubStars}
        {body}
      </div>
    );
  }
}


