import React, { Component } from "react";

class NewsItem extends Component {
  render() {
    const date = this.props.date;
    const content = this.props.children;

    return (
      <p className="date">
        <span>&bull;{" "}</span>
        <b>{"["}{date}{"] "}</b>
        {content}
      </p>
    );
  }
}

export default NewsItem;