import React, { Component } from "react";
import GitHubButton from 'react-github-btn';

class GithubBtn extends Component {
  render() {
    
    if (!this.props.href) return null;

    // https://buttons.github.io/

    return (
      <GitHubButton href={this.props.href} data-show-count="true" aria-label="Star buttons/github-buttons on GitHub">Star</GitHubButton>
    );
  }
}

export default GithubBtn;
