import React, { Component } from "react";
import Slide from "react-reveal";
import Zmage from "react-zmage";


let id = 0;
class PeopleItem extends Component {
  render() {
    const name = this.props.name;
    const position = this.props.position;
    const strName = name.replace(" ", "_")
    let image;
    if (this.props.altimage){
      image = "images/people/" + this.props.altimage;
    } else {
      image = "images/people/" + strName + ".jpg";
    }

    let link;
    if (this.props.url){
      link = <div><a href={this.props.url}>{name}</a></div>
    }else{
      link = <div>{name}</div>
    }

    return (
      <div key={id++} className="columns portfolio-item">
        <div className="item-wrap">
          <div id="image-wrap" style={{"position": "relative"}}>
            <div style={{"position": "absolute", "width": "100%", "height": "100%", "objectFit": "cover", "verticalAlign": "middle"}}>
              <span style={{
                "display": "inline-block",
                "height": "100%",
                "verticalAlign": "middle"
              }}></span>
              <Zmage alt="missing image" src={image} style={{"verticalAlign": "middle"}} />
            </div>
            <div style={{"paddingBottom": "150%"}}></div>
          </div>
          <div style={{ textAlign: "center", "backgroundColor": "#f8f8f8f8"}}>
            {link}
            {position}
          </div>
        </div>
      </div>
    );
  }
}


class AlumnusItem extends Component {
  render() {
    const name = this.props.name;
    const strName = name.replace(" ", "_")
    const position = this.props.position;

    if (this.props.url){
      return <div key={strName}>
        <a href={this.props.url}>{name}</a>: {position}
      </div>
    }else{
      return <div key={strName}>
        <span>{name}</span>: {position}
      </div>
    }
  }
}




