import React, { Component } from "react";
import ReactGA from "react-ga";
// import "./App.css";
import { BrowserRouter, Route, Routes } from "react-router-dom"
import HomePage from "./Pages/Home";
import HakePage from "./Pages/Hake";
import OclPage from "./Pages/Ocl";

import ExampleSubpage from "./Pages/Subpages/Example";


class App extends Component {
  constructor(props) {
    super(props);
    // this.state = {
    //   foo: "bar",
    //   textData: {}
    // };

    ReactGA.initialize("UA-110570651-1");
    ReactGA.pageview(window.location.pathname);
  }

  render() {
    return (
      <BrowserRouter>
        <Routes>
          <Route path="/" element={<HomePage />} />
          <Route path="/ocl" element={<OclPage />} />
          <Route path="/hake" element={<HakePage />} />
          <Route path="/pub/example" element={<ExampleSubpage />} />
        </Routes>
      </BrowserRouter>
    );
  }
}

export default App;