import React, { Component } from "react";
import Slide from "react-reveal";
import Zmage from "react-zmage";
import Fade from "react-reveal";

import HakeBtn from "../Items/HakeBtn";


class ProjectItem extends Component {
  render() {
    const shortname = this.props.short;
    const fullname = this.props.full;
    const year = this.props.year;
    const url = this.props.url;
    const description = this.props.children;

    return (
      <div key={shortname}>
        <a href={url}><h3 style={{"display": "inline"}}>{shortname}</h3> <i className="fa fa-external-link" style={{"height": "0.5em", "verticalAlign": "top"}}></i></a>
        <p className="info">
          {fullname} <span>&bull;</span>
          <em className="date">{year}</em>
        </p>
        <p>{description}</p>
      </div>
    );
  }
}


let id = 0;
class PortfolioItem extends Component {
  render() {
    const name = this.props.name;
    const image = "images/portfolio/" + this.props.image;

    let href;
    if(this.props.url) {
      href = (<a href={this.props.url}>{name}</a>);
    }else{
      href = (<span>{name}</span>);
    }
    
    return (
      <div key={id++} className="columns portfolio-item">
        <div className="item-wrap">
          <div id="image-wrap" style={{"position": "relative"}}>
            <div style={{"position": "absolute", "width": "100%", "height": "100%", "objectFit": "cover", "verticalAlign": "middle"}}>
              <span style={{
                "display": "inline-block",
                "height": "100%",
                "verticalAlign": "middle"
              }}></span>
              <Zmage alt="missing image" src={image} style={{"verticalAlign": "middle"}} />
            </div>
            <div style={{"paddingBottom": "100%"}}></div>
          </div>
          <div style={{ textAlign: "center", "backgroundColor": "#f8f8f8f8"}}>{href}</div>
        </div>
      </div>
    );
  }
}




